@font-face {
	font-family: 'Pixel';
	src: local('Pixel'),
		url(./fonts/exepixelperfect.medium.ttf) format('truetype');
}

* {
	font-family: 'Pixel';
}

.App {
	background-color: black;
	background-image: url(./assets/images/FondoMenu_7x.png);
	background-size: contain;
	overflow: overlay;
}

/* Empuja el footer al fondo de la p√°gina, mantener */
.wrapper {
	min-height: calc(100vh - 5.95rem);
	/*background-color: #000000;*/
	color: antiquewhite;
}

/* width */
::-webkit-scrollbar {
	width: 20px;
	background-size: contain;
	background-color: transparent;
}

/* Track */
::-webkit-scrollbar-track {
	background-image: url(./assets/UI/scroll_fondo_palo_2_31x.png);
	background-size: contain;
	background-color: transparent;
	border-radius: 10px;
	border: 1px solid black;
}

::-webkit-scrollbar-button:vertical:decrement {
	background-image: url(./assets/UI/flecha_scroll.png);
	background-repeat: no-repeat;
	background-size: contain;
	background-color: transparent;
}

::-webkit-scrollbar-button:vertical:increment {
	background-image: url(./assets/UI/flecha_scroll_180.png);
	background-repeat: no-repeat;
	background-size: contain;
	background-color: transparent;
}

::-webkit-scrollbar-thumb {
	background-image: url(./assets/UI/scrollbar_tile_3_60x.png);
	background-size: contain;
	background-color: transparent;
	border-radius: 15px;
	border: 2.5px solid black;
}

.btn-default {
	background-repeat: no-repeat;
	background-size: cover;
	background-color: orange !important;
	color: #444 !important;
	/*border: 2.5px solid black;*/
}

.cont-estandar {
	padding-bottom: 0.8rem;
	background: repeating-linear-gradient(
		0deg,
		#ebc486,
		#ebc486 20px,
		#f6d196 20px,
		#f6d196 40px
	);
	border-radius: 1rem !important;
	border: 0.2rem solid #7c643c;
	color: #444;
	margin-bottom: 2rem;
}
